<template>
  <section :class="forToggle" class="boards-nav-bar">
    <el-tooltip
      class="item"
      effect="dark"
      :content="isOpen ? 'close' : 'open'"
      placement="right-start"
    >
      <div class="toggle-board-nav-bar">
        <input v-model="isOpen" type="checkbox" />
        <i :class="toggleStyle"></i>
      </div>
    </el-tooltip>
    <div v-if="isOpen">
      <h3>Workspace</h3>
      <div class="workspace-header">
        <div class="flex txt">
          <el-avatar
            shape="square"
            :size="'small'"
            :src="squareUrl"
          ></el-avatar>
          <h4>Main Workspace</h4>
        </div>
        <i class="el-icon-arrow-down"></i>
      </div>
      <ul class="board-list">
        <li @click="open" class="board-nav-item">
          <h4><i class="el-icon-plus"></i> New Board</h4>
        </li>
        <li class="board-nav-item">
          <h4><i class="el-icon-star-off"></i> Starred</h4>
        </li>
        <li class="board-nav-item">
          <h4><i class="el-icon-search"></i> Search</h4>
        </li>
        <el-divider></el-divider>
        <li class="" v-for="board in boardsList" :key="board._id">
          <board-nav-item :navItem="board" />
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
import boardNavItem from "./board-nav-item.vue";
export default {
  data() {
    return {
      currBoardId: this.$route.params.boardId,
      newBoardNmae: "",
      isOpen: true,
      squareUrl:
        "data:image/png;base64,",
    };
  },

  methods: {
    async newBoard() {
      var newBoard = await this.$store.dispatch({
        type: "newBoard",
        boardName: this.newBoardNmae,
      });
      this.$router.push("/board/" + newBoard._id);
    },
    open() {
      this.$prompt("Please input name", "Add new board", {
        confirmButtonText: "ADD",
        cancelButtonText: "Cancel",
      })
        .then(({ value }) => {
          this.newBoardNmae = value;
          this.newBoard();
          this.$message({
            type: "success",
            message: `The "${value}" board was successfully added`,
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Input canceled",
          });
        });
    },
  },
  computed: {
    toggleStyle() {
      return {
        "el-icon-arrow-left": this.isOpen,
        "el-icon-arrow-right": !this.isOpen,
      };
    },
    forToggle() {
      return {
        close: !this.isOpen,
        "": this.isOpen,
      };
    },
    btnTxt() {
      return {
        open: !this.isOpen,
        colse: this.isOpen,
      };
    },
    boardsList() {
      return this.$store.getters.boardsList;
    },
  },
  components: {
    boardNavItem,
  },
};
</script>

<style></style>
